import React, {useEffect} from 'react';
import "../assets/css/about.css"
import {motion, useAnimation} from "framer-motion";
import {useInView} from "react-intersection-observer";

const About = () => {
    const headerControl = useAnimation();
    const [headerRef, headerInView] = useInView();
    const [contentRef, contentInView] = useInView();

    const contentControl = useAnimation();
    const transition = {
        duration: 2,
        ease: "easeInOut"
    };

    const sequence = async () => {
        await headerControl.start("visible");
        return await contentControl.start("visible");
    };

    useEffect(() => {
        if (headerInView) {
            sequence();
        } else {
            headerControl.start("hidden");
            contentControl.start("hidden");
        }
    }, [headerControl, headerInView, contentControl, contentInView]);

    return (
        <div id={"about"}>
            <div>
                <motion.div
                    id={"header"}
                    ref={headerRef}
                    animate={headerControl}
                    initial="hidden"
                    transition={transition}
                    variants={{
                        visible: {
                            opacity: 1,
                            x: "0",
                        },
                        hidden: {
                            opacity: 0,
                            x: "-100%",
                            transition: {duration: 1}
                        },
                    }}
                >
                    <h1>
                        ABOUT ME
                        <p className={"linebreak"}><hr/></p>
                    </h1>
                </motion.div>
                <motion.p
                    id={"content"}
                    ref={contentRef}
                    animate={contentControl}
                    transition={transition}
                    initial="hidden"
                    variants={{
                        visible: {
                            opacity: 1,
                        },
                        hidden: {
                            opacity: 0,
                            transition: {duration: 1}
                        },
                    }}

                >Ian Parcon loves programming, reading books and articles. During his spare time he
                    is
                    making games. He already
                    has
                    a 2 years job
                    experience with back-end development and mobile development.
                </motion.p>
            </div>
        </div>
    )
};
export default About;